@mixin flex($display, $justify-content: none, $align-items: none) {
  display: $display;
  justify-content: $justify-content;
  align-items: $align-items;
}

@mixin width-height($width, $height: $width) {
  width: $width;
  height: $height;
}

@mixin top-left($top: none, $left: $top) {
  top: $top;
  left: $left;
}

@mixin top-right($top: none, $right: $top) {
  top: $top;
  right: $right;
}

